import { vec3, mat4 } from 'gl-matrix';
import { createTransforms, createGPUBuffer, loadWGSL } from '../../lib';

import { Geometry } from './Geometry';

import { Scene } from '../../Scene';
import { ObjectBase } from '../Base';

const shaderPath = require('./shader.wgsl');

export class NGeojsonLine extends ObjectBase {
    isRenderable= false;

    numberOfVertices: number = 0;
    vertexBuffer;
    colorBuffer;
    normalBuffer;
    uvBuffer;
    uniformBindGroup;
    uniformBindGroup1;
    uniformBindGroup2;
    uniformBuffer;

    commandEncoder;

    textureView;
    renderPassDescription;
    pipeline: GPURenderPipeline;

    isAnimation = false;

    modelMatrix: mat4;
    translation: vec3 = vec3.fromValues(0,0,0);
    rotation: vec3 = vec3.fromValues(0,0,0);

    mouseBuffer;
    frameBuffer;

    t: number = 0;

    resultBuffer;

    geometry: Geometry;

    constructor(scene: Scene) {
        super(scene)

        this.initPipeline();

    }
    async initPipeline () {
        this.geometry = new Geometry();
        await this.geometry.init();
        console.log(this.geometry)

        // create vertex buffers
        // const sphereData = SphereData(1.5, 70, 40);

        // console.log(sphereData);

        this.numberOfVertices = (this.geometry.pos!).length/3;
        // console.log(this.numberOfVertices);
        this.vertexBuffer = createGPUBuffer(this.webGPU.device, this.geometry.pos);   
        this.colorBuffer = createGPUBuffer(this.webGPU.device, this.geometry.colors);
        // this.normalBuffer = createGPUBuffer(this.webGPU.device, this.pos?.normalData!);



        
        // const bindGroupLayout = this.webGPU.device.createBindGroupLayout({
        //     entries: [
        //       {
        //         binding: 0,
        //         visibility: GPUShaderStage.VERTEX,
        //         type: "uniform-buffer",
        //       },
        //     ],
        //   });

        //   const bindGroupLayout = this.webGPU.device.createBindGroupLayout({
        //     entries: [
        //       {
        //         binding: 0,
        //         visibility: GPUShaderStage.VERTEX,
        //         buffer: {
        //           type: "uniform",
        //         },
        //       },
        //     ],
        //   });


        // const mouseBuffer = device.createBuffer({
        //     size: 8,
        //     usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
        //   });

        // this.mouseBuffer = createGPUBuffer(this.webGPU.device, this.scene.app.controller.eventController.mNormalized);

        // this.numberOfVertices = (sphereData?.vertexData!).length/3;
        // this.vertexBuffer = createGPUBuffer(this.webGPU.device, sphereData?.vertexData!);   
        // this.normalBuffer = createGPUBuffer(this.webGPU.device, sphereData?.normalData!);
        // this.uvBuffer = createGPUBuffer(this.webGPU.device, sphereData?.uvData!);
        const shader = (await loadWGSL(shaderPath)) as string;
        // console.log(shader)
        this.pipeline = this.webGPU.device.createRenderPipeline({
            layout: 'auto',
            vertex: {
                module: this.webGPU.device.createShaderModule({ code: shader }),
                entryPoint: "vs_main",
                // @ts-ignore
                buffers: [
                    {
                        arrayStride: 4 * 3,
                        attributes: [{
                            shaderLocation: 0,
                            format: "float32x3",
                            offset: 0
                        }]
                    },
                    {
                        arrayStride: 4 * 3,
                        attributes: [{
                            shaderLocation: 1,
                            format: "float32x3",
                            offset: 0
                        }]
                    }, 

                ]
            },
            fragment: {
                module: this.webGPU.device.createShaderModule({ code: shader }),
                entryPoint: "fs_main",
                targets: [
                    {
                        format: this.webGPU.format as GPUTextureFormat
                    }
                ]
            },
            primitive: {
                // https://learn.microsoft.com/en-us/windows/win32/direct3d11/d3d10-graphics-programming-guide-primitive-topologies
                topology: this.webGPU.PrimitiveTopology.LineList // TriangleStrip // .PointList, //  "line-strip", //"line-list",
            },
            depthStencil: {
                format: "depth24plus",
                depthWriteEnabled: true,
                depthCompare: "less"
            }
        });

        // // create uniform data
        this.modelMatrix = mat4.create();
        // const mvpMatrix = mat4.create();
        // let vMatrix = mat4.create();
        // let vpMatrix = mat4.create();
        // const vp = CreateViewProjection(gpu.canvas.width / gpu.canvas.height);
        // vpMatrix = vp.viewProjectionMatrix;

        // add rotation and camera:
        this.rotation = vec3.fromValues(0, 0, 0);
        // var camera = createCamera(gpu.canvas, vp.cameraOption);

        // create uniform buffer and layout
        this.uniformBuffer = this.webGPU.device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST
        });

        this.mouseBuffer = this.webGPU.device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
        });

        this.frameBuffer = this.webGPU.device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST,
        });

        this.uniformBindGroup = this.webGPU.device.createBindGroup({
            layout: this.pipeline.getBindGroupLayout(0),
            entries: [{
                binding: 0,
                resource: {
                    buffer: this.uniformBuffer,
                    offset: 0,
                    size: 64
                }
            },

            ]
        });


        // var bindGroupLayout = this.webGPU.device.createBindGroupLayout({
        //     entries: [
        //         {binding: 0, visibility: GPUShaderStage.VERTEX, buffer: {type: "uniform"}}]
        // });


        this.uniformBindGroup1 = this.webGPU.device.createBindGroup({
            layout: this.pipeline.getBindGroupLayout(1), // bindGroupLayout,
            entries: [
              {
                binding: 0,
                resource: {
                  buffer: this.mouseBuffer,
                },
              },
              
            ],
        });


        this.textureView = this.webGPU.ctx.getCurrentTexture().createView();
        const depthTexture = this.webGPU.device.createTexture({
            size: [this.webGPU.ctx.canvas.width, this.webGPU.ctx.canvas.height, 1],
            format: "depth24plus",
            usage: GPUTextureUsage.RENDER_ATTACHMENT
        });
        this.renderPassDescription = {
            colorAttachments: [{
                view: this.textureView,
                clearValue: { r: 0.2, g: 0.247, b: 0.314, a: 1.0 }, //background color
                loadValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 },
                loadOp: 'clear',
                storeOp: 'store'
            }],
            depthStencilAttachment: {
                view: depthTexture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: "store",
                //stencilLoadValue: 0,
                //stencilStoreOp: "store"
            }
        };


        this.isRenderable = true;
    }
    async render() {
        if (!this.isRenderable) { return; }

        // const commandEncoder = device.createCommandEncoder()
        // const computePass = commandEncoder.beginComputePass()
        // computePass.setPipeline(pipelineObj.computePipeline)
        // computePass.setBindGroup(0, pipelineObj.computeGroup)
        // computePass.dispatchWorkgroups(Math.ceil(NUM / 128))
        // computePass.end()


        // this.translation[0] += 0.005;
        // this.rotation[1] += 0.005;

        // console.log(this.translation)
        // console.log(this.rotation)
        // console.log(this.modelMatrix)

        this.webGPU.device.queue.writeBuffer(this.mouseBuffer, 0, new Float32Array([this.scene.app.controller.eventController.mNormalized[0], this.scene.app.controller.eventController.mNormalized[1], this.t]));
        
        this.webGPU.device.queue.writeBuffer(this.frameBuffer, 0, new Float32Array([this.t, 0]));

        // const mouseData = new Float32Array([mouseX, mouseY]);
        // device.queue.writeBuffer(mouseBuffer, 0, mouseData);

        // console.log(this.scene.app.controller.eventController.mNormalized)

        // updateMouseBuffer(this.mouseBuffer, this.scene.app.controller.eventController.mNormalized[0], this.scene.app.controller.eventController.mNormalized[1])


        if (false) {
            this.geometry.pos[2] = Math.sin(this.t);
            this.webGPU.device.queue.writeBuffer(this.vertexBuffer, 0, this.geometry.pos )
        }


        createTransforms(this.modelMatrix, this.translation, this.rotation);
        mat4.multiply(this.modelMatrix, this.webGPU.renderer.cameraController.getMatrix, this.modelMatrix);
        this.webGPU.device.queue.writeBuffer(this.uniformBuffer, 0, this.modelMatrix as ArrayBuffer);
        
        // this.webGPU.device.queue.writeBuffer(this.uniformBuffer, 0, this.webGPU.renderer.cameraController.camera.projectionMatrix as ArrayBuffer);
        // this.webGPU.device.queue.writeBuffer(this.uniformBuffer, 0, this.webGPU.renderer.camera.mvpMatrix as ArrayBuffer);


        // contextTexture.destroy();
        // texture.destroy();
        
        // contextTexture = gpu.context.getCurrentTexture();      
        // texture = device.createTexture({
        //     size: [gpu.canvas.width, gpu.canvas.height, 1],
        //     sampleCount: 4,
        //     format: gpu.format as GPUTextureFormat,
        //     usage: GPUTextureUsage.RENDER_ATTACHMENT,
        // });
        // renderPassDescription.colorAttachments[0].resolveTarget = contextTexture.createView();
        // renderPassDescription.colorAttachments[0].view = texture.createView() as GPUTextureView;

        // const commandEncoder = this.webGPU.device.createCommandEncoder();
        // // const renderPass = commandEncoder.beginRenderPass(renderPassDescription as GPURenderPassDescriptor);
        // this.textureView = this.webGPU.ctx.getCurrentTexture().createView();
        // this.renderPassDescription.colorAttachments[0].view = this.textureView;
        // const renderPass = commandEncoder.beginRenderPass(this.renderPassDescription as GPURenderPassDescriptor);

        this.webGPU.renderPass.setPipeline(this.pipeline);
        this.webGPU.renderPass.setVertexBuffer(0, this.vertexBuffer);
        this.webGPU.renderPass.setVertexBuffer(1, this.colorBuffer);

        this.webGPU.renderPass.setBindGroup(0, this.uniformBindGroup);
        this.webGPU.renderPass.setBindGroup(1, this.uniformBindGroup1);
        // this.webGPU.renderPass.setBindGroup(2, this.uniformBindGroup2);

        this.webGPU.renderPass.draw(this.numberOfVertices);

        this.t += 0.01;

        // const resultBufferArrayBuffer = await this.resultBuffer.mapReadAsync();
        // const pickedObjectID = new Uint32Array(resultBufferArrayBuffer)[0];
        // console.log(pickedObjectID)
        // this.webGPU.renderPass.end();
        // this.webGPU.device.queue.submit([this.commandEncoder.finish()]);
    }
}
