import { Float32BufferAttribute } from 'three';
import { Controller } from '.';
export class EventController {
    public controller: Controller;
    public pos = [0, 0]
    public mNormalized = new Float32Array([0,0]);
    public isDown = false
    constructor(controller: Controller) {
        this.controller = controller;

    }
    public init() {
        this.appendEvent();
    }
    public appendEvent() {
        this.controller.app.renderer.canvas.addEventListener('ondrag', (e: MouseEvent) => {
            // console.log('click', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('click', (e: MouseEvent) => {
            // console.log('click', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('dblclick', (e: MouseEvent) => {
            // console.log('d-click', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('drag', (e: MouseEvent) => {
            // console.log('drag', e);
        }, true);
        this.controller.app.renderer.canvas.addEventListener('mouseup', (e: MouseEvent) => {
            // console.log('mouse up', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('mousedown', (e: MouseEvent) => {
            this.isDown = true;
            this.pos[0] = e.offsetX;
            this.pos[1] = e.offsetY;

            // canvas.addEventListener("mousemove", (event) => {
    
            //   });
            // this.controller.app.renderer.camera.translateX(0.25)
            // this.controller.app.renderer.cameraController.translateX();
            // this.controller.app.renderer.cameraController.rotateZ();
            // console.log('pointer down', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('mouseup', (e: MouseEvent) => {
            // console.log('pointer up', e);
            this.isDown = false;
        }, false);
        this.controller.app.renderer.canvas.addEventListener('mousemove', (e: MouseEvent) => {
            if (this.isDown) {
                // console.log('move dragging')
                const xOff = this.pos[0] - e.offsetX;
                const yOff = this.pos[1] - e.offsetY;

                this.controller.app.renderer.cameraController.moveCamera(xOff, yOff)
                this.pos[0] = e.offsetX;
                this.pos[1] = e.offsetY;



            } else {
                // console.log('move')

                const rect = this.controller.app.renderer.canvas.getBoundingClientRect();
                this.mNormalized[0] = (e.clientX - rect.left) / this.controller.app.renderer.canvas.width;
                this.mNormalized[1] = (e.clientY - rect.top) / this.controller.app.renderer.canvas.height;
                // console.log(this.mNormalized)
            

            }

            // console.log('mousemove', e);
        }, false);
        this.controller.app.renderer.canvas.addEventListener('wheel', (e:  WheelEvent) => {
            // console.log('wheel', e.deltaY);
            this.controller.app.renderer.cameraController.zoomWheel(e.deltaY)
        }, false);

        window.addEventListener('keydown', (e:  KeyboardEvent) => {
            this.controller.keyboard(e);
        }, false);
    }
}