import { getJSONFromURL, MercatorProjection } from 'njscore';

export class Geometry {
    streetOSM: any = [];

    pos: any;
    colors: any;

    constructor() {

    }
    async init() {
        await getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/street_osm.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.streetOSM.push(data['features'][i]['geometry']['coordinates']);
            }
            this.renderLines(this.streetOSM, [0, 1, 1]);
        });
        // this.pos = new Float32Array([0, 0, 0,   0, 0.5, 0,   0.5, 0.5, 1,    0, 0, 0,  0.5, 0.5, 1,  0.5, 0, 0, ]);
    }
    renderLines = (paths: any, color: number[]) => {

        const positions = [];
        const colors = [];
        const indices = [];

        const xOFf =  15478749.591872748 // 48.85635555014451; -15478803.88731685; 15478749.591872748 4766690.415614613 // 15478749.591872748 4766690.415614613
        const yOFf =  4766690.415614613 // -4766681.448690384;
        const scale = 0.0001;


        let xC = 0
        let yC = 0
        let Count = 0

        for (let i = 0; i < paths.length; ++i) {
            const route = paths[i]

            for (let j = 0; j < route.length; ++j) {
                for (let k = 0; k < route[j].length - 1; ++k) {

                    const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);
                    // xC += xPro;
                    // yC += yPro;

                    const [xPro1, yPro1] = MercatorProjection(route[j][k+1][0], route[j][k+1][1]);
                    // const xPro = route[j][k][0];
                    // const yPro = route[j][k][1];

                    // const xPro1 = route[j][k+1][0];
                    // const yPro1 = route[j][k+1][1];

                    xC += xPro;
                    yC += yPro;

                    Count += 1;

                    const x = (xPro - xOFf) * scale
                    const y = (yPro - yOFf) * scale

                    const x1 = (xPro1 - xOFf) * scale
                    const y1 = (yPro1 - yOFf) * scale

                    // console.log(x, y)
                    positions.push(x, y, 0)

                    colors.push(color[0], color[1],  color[2]);

                    // if (k !== 0) {
                        positions.push(x1, y1, 0)
                        // positions.push(x, y, Math.random() * 0.01)
                        colors.push(color[0], color[1],  color[2]);
                    // }

                    if (k < route[j].length - 1) {
                        indices.push(Math.floor(positions.length / 3) - 1, Math.floor(positions.length / 3));
                    }
                }

                // positions.push(positions[positions.length-3], positions[positions.length-2], positions[positions.length-1])
                // colors.push(color[0], color[1],  color[2]);

                // positions.push(0, 0, 0)
                // colors.push(0,0,0);
            }
        }

        console.log(positions)
        console.log(xC / Count, yC / Count )
        // geometry.setIndex(indices);
        // geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
        // geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
        // // generateMorphTargets( geometry );

        // geometry.computeBoundingSphere();

        // const line = new THREE.LineSegments(geometry, material);
        // scene.add(line);

        this.pos = new Float32Array(positions.flat());
        this.colors = new Float32Array(colors.flat());
        // this.pos = new Float32Array([0, 0, 0,   0, 0.5, 0,   0.5, 0.5, 1,    0, 0, 0,  0.5, 0.5, 1,  0.5, 0, 0, ]);
    };
}