import { TriMeshUniform } from "../Object/TriMeshUniform";
import { SphereWire } from '../Object/SphereWire';
import { Box } from "../Object/Box";
import { BoxF } from "../Object/BoxF";
import { PolylineRandom } from "../Object/PolylineRandom";

export class Scene {
    public app: App;
    public objects: ObjectBase[] = [];

    independentPipeline = false;
    
    public t = 0.0;
    constructor(app: App) {
        this.app = app;

        
    }
    async init() {

        // new TriMeshUniform(this);


        // new Box(this);
        // new SphereWire(this);

        // new BoxF(this);
        // new TriangleMesh(this);


        
        // new NBox(this)
        new NSphereWire(this)

        new NGeojsonPoint(this);
        new NGeojsonLine(this);

        // new Quad(this);

        // new ExShader(this);

        // new PolylineRandom(this);
    }
    public render() {
        if (this.independentPipeline) {
            this.renderObject();
        } else {
            this.renderScene();
        }
        this.t+= 1;
    }
    public renderObject() {
        for (let i = 0 ; i < this.objects.length; ++i) {
            this.objects[i].update();
            this.objects[i].render();
        }
    }
    public renderScene() {
        // this.webGPU.device.queue.writeBuffer(this.uniformBuffer, 0, this.modelMatrix as ArrayBuffer);
        this.app.renderer.webGPU.textureView = this.app.renderer.webGPU.ctx.getCurrentTexture().createView();
        this.app.renderer.webGPU.renderPassDescription.colorAttachments[0].view = this.app.renderer.webGPU.textureView;
        this.app.renderer.webGPU.commandEncoder = this.app.renderer.webGPU.device.createCommandEncoder();
        this.app.renderer.webGPU.renderPass = this.app.renderer.webGPU.commandEncoder.beginRenderPass(this.app.renderer.webGPU.renderPassDescription as GPURenderPassDescriptor);

        for (let i = 0 ; i < this.objects.length; ++i) {
            this.objects[i].update();
            this.objects[i].render();
        }


        if (this.t % 300 === 0) {
            // new TriMeshUniform(this);
            // new Box(this);
            // console.log('ddd');
        }


        this.app.renderer.webGPU.renderPass.end();
        this.app.renderer.webGPU.device.queue.submit([this.app.renderer.webGPU.commandEncoder.finish()]);
    }
}