import { Renderer } from '.';

export enum PrimitiveTopology {
    PointList = 'point-list',
    LineList = 'line-list',
    LineStrip = 'line-strip',
    TriangleList = 'triangle-list',
    TriangleStrip = 'triangle-strip',
}

export class WebGPU {
    public renderer: Renderer;

    ctx: GPUCanvasContext;
    device: GPUDevice;
    format: GPUTextureFormat;

    commandEncoder: GPUCommandEncoder;
    renderPass: GPURenderPassEncoder;

    textureView: GPUTextureView;

    PrimitiveTopology = PrimitiveTopology;


    renderPassDescription: GPURenderPassDescriptor;

    depthTexture : GPUTexture;

    constructor(renderer: Renderer) {
        this.renderer = renderer;
    }
    public async initWebGPU() {
        if (!navigator.gpu) { throw new Error('Not Support WebGPU'); }
        const adapter = await navigator.gpu.requestAdapter({
            powerPreference: 'high-performance'
            // powerPreference: 'low-power'
        })
        if (!adapter) { throw new Error('No Adapter Found'); }

        const device = await adapter.requestDevice()
        this.ctx = this.renderer.canvas.getContext('webgpu') as GPUCanvasContext
        // this.format = navigator.gpu.getPreferredCanvasFormat ? navigator.gpu.getPreferredCanvasFormat() : this.ctx.getPreferredFormat(adapter)
        this.format = "bgra8unorm";

        this.ctx.configure({
            device: device, 
            format: this.format,
            // prevent chrome warning
            alphaMode: 'opaque'
        });

        // this.size = { width: this.canvas.width, height: this.canvas.height }
        this.device = device;

        this.textureView = this.ctx.getCurrentTexture().createView();
        this.depthTexture = this.device.createTexture({
            size: [this.ctx.canvas.width, this.ctx.canvas.height, 1],
            format: "depth24plus",
            usage: GPUTextureUsage.RENDER_ATTACHMENT
        });
        // this.renderPassDescription = {
        //     colorAttachments: [{
        //         view: this.textureView,
        //         clearValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 }, //background color
        //         loadOp: 'clear',
        //         storeOp: 'store',
        //         //@ts-ignore
        //         loadValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 },
        //     }],
        //     depthStencilAttachment: {
        //         view: this.depthTexture.createView(),
        //         depthClearValue: 1.0,
        //         depthLoadOp: 'clear',
        //         depthStoreOp: "store",
        //     }
        // };
        this.renderPassDescription = {
            // @ts-ignore
            colorAttachments: [{
                view: this.textureView,
                clearValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 }, //background color
                loadOp: 'clear',
                storeOp: 'store'
            }],
            depthStencilAttachment: {
                view: this.depthTexture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: "store",
                //stencilLoadValue: 0,
                //stencilStoreOp: "store"
            }
        }
    }
    public destroy() {
        this.device.destroy();
    }
}

